export default {
	data() {
		return {
			
		}
	},
	methods: {
		img(){
		                uni.chooseImage({
		                    // count:  允许上传的照片数量
		                    count:5,
		                    // sizeType:  original 原图，compressed 压缩图，默认二者都有
		                    sizeType: "original",
		                    success(res){
		                        console.log(res)
		                        uni.previewImage({
		                            // 对选中的图片进行预览
		                            urls: res.tempFilePaths,
		                            // urls:['','']  图片的地址 是数组形式
		                        })
		                    } 
		                });
		            },
		
	}
}
